import {xml2js} from "xml-js";

export let articleGroups = {};
export let ready = 0;

export async function getArticlesGroups() {
    if (ready === 1) return articleGroups;
    return new Promise((resolve, reject) => {
        if (ready === -1) return reject();
        setInterval(() => {
            if (ready === 1) return resolve(articleGroups);
            if (ready === -1) return reject();
        }, 200);
    });
}

(async () => {
    try {
        let response = await fetch("/articleGroups.json");
        if (response.ok) {
            articleGroups = await response.json();
            ready = 1;
        }
        else ready = -1;
    } catch (error) {
        ready = -1;
    }
})();

export async function getArticle(group, article) {
    return new Promise(async (resolve, reject) => {
        try {
            let response = await fetch(`/articleGroups/${group}/${article}/article.xml`);
            if (response.ok) resolve(xml2js(await response.text()));
            else reject();
        } catch (error) {
            reject(error);
        }
    });
}